package org.example.eparish.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.functions.linearGradient
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.core.rememberPageContext
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.forms.Input
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.graphics.ImageLoading
import org.example.eparish.components.Routes
import org.example.eparish.components.eparishColors
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.*

@Page("/login")
@Composable
fun Login() {
    val ctx = rememberPageContext()
    var email by remember { mutableStateOf("") }
    var password by remember { mutableStateOf("") }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .backgroundImage(linearGradient(eparishColors.secondary, eparishColors.third))
            .padding(16.px),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {

        // Responsive Card
        Box(
            modifier = Modifier
                .maxWidth(420.px)
                .fillMaxWidth()
                .backgroundImage(linearGradient(eparishColors.third, eparishColors.primary))
                .borderRadius(14.px)
                .padding(24.px)
                .boxShadow(
                    offsetX = 0.px,
                    offsetY = 6.px,
                    blurRadius = 18.px,
                    color = eparishColors.third

                ),
            contentAlignment = Alignment.Center
        ) {

            Column(
                verticalArrangement = Arrangement.Center,
                horizontalAlignment = Alignment.CenterHorizontally,
                modifier = Modifier.fillMaxWidth()
            ) {

                // Logo
                Image(
                    src = "logo.png",
                    loading = ImageLoading.Eager,
                    alt = "Logo",
                    modifier = Modifier
                        .size(120.px)
                        .margin(bottom = 16.px)
                )
                Row(
                    Modifier.color(eparishColors.primary)
                        .fontSize(1.2.em)
                        .fontWeight(FontWeight.Bold).margin(bottom = 16.px)

                ) {
                    Text("Karibu e-parish")
                }

                Br()

                // --- INPUT STYLE (Reusable) ---
                val inputStyle = Modifier
                    .fillMaxWidth()
                    .padding(6.px)
                    .borderRadius(10.px)
                    .padding(12.px)
                    .margin(topBottom = 8.px)
                    .background(eparishColors.text)
                    .boxShadow(
                        offsetX = 0.px,
                        offsetY = 3.px,
                        blurRadius = 10.px,
                        color = Color.black
                    )

                // Email Input
                Label(forId = "email",
                    attrs = Modifier.align(Alignment.Start)
                        .color(eparishColors.primary)
                        .fontWeight(FontWeight.Bold)
                        .toAttrs())
                { Text("e-mail ya mtumiaji") }
                Input(
                    type = InputType.Text,
                    value = email,
                    onValueChange = {
                        if (it.length <= 35) email = it
                    },
                    modifier = inputStyle.id("email")
                )

                Br()

                // Password Input
                Label(forId = "password" ,attrs = Modifier.align(Alignment.Start)
                    .color(eparishColors.primary)
                    .fontWeight(FontWeight.Bold)
                    .toAttrs()) { Text("Nenosiri") }
                Input(
                    type = InputType.Password,
                    value = password,
                    onValueChange = {
                        if (it.length <= 35) password = it
                    },
                    modifier = inputStyle.id("password")
                )

                Br()

                // Button
                Button(
                    modifier = Modifier
                        .fillMaxWidth()
                        .padding(top = 12.px)
                        .margin(8.px)
                        .borderRadius(12.px)
                        .padding(12.px)
                        .color(eparishColors.text)
                        .backgroundImage(
                            linearGradient(
                                eparishColors.primary,
                                eparishColors.secondary
                            )
                        )
                        .boxShadow(
                            offsetX = 0.px,
                            offsetY = 4.px,
                            blurRadius = 14.px,
                        ),
                    onClick = {
                        //go to the home page
                        ctx.router.navigateTo(Routes.home)
                    }
                ) {
                    Text("Ingia")
                }
            }
        }
    }
}
