            .margin(top = 16.px, bottom = 16.px)
    ) {

        Row(
            modifier = Modifier
                .fillMaxWidth()
                .fontSize(22.px)
                .fontWeight(FontWeight.Bold)
                .color(eparishColors.text),
            horizontalArrangement = Arrangement.Start,
            verticalAlignment = Alignment.CenterVertically
        ) {
            Text("Orodha ya Viongozi")

            // pushes the button to the far right
            Span(attrs = Modifier.weight(1f).toAttrs())

            Button(
                modifier = buttonStyle.toModifier(),
                onClick = { /* TODO: Add leader */ }
            ) {
                Row(
                    verticalAlignment = Alignment.CenterVertically,
                    horizontalArrangement = Arrangement.spacedBy(6.px)
                ) {
                    FaPlus(size = IconSize.XL, modifier = Modifier.color(eparishColors.text))

                    if (isLargeScreen) {
                        Text("Ongeza Kiongozi")
                    }
                }
            }
        }

        // TODO: Add your leaders list here
