@OptIn(DelicateApi::class)
@Composable
fun Catholics() {
    val isLargeScreen = rememberBreakpoint() >= Breakpoint.LG
    var sajiliMkatoliki by remember { mutableStateOf(false) }

    Column(
        modifier = Modifier
            .fillMaxWidth()                          //  allow full width
            .color(eparishColors.primary)            //  full background like Dashboard
            .borderRadius(12.px)
            .padding(topBottom = 12.px, leftRight = 16.px)
            .margin(top = 16.px, bottom = 16.px)
    ) {

        if (!sajiliMkatoliki) {

            Row(
                modifier = Modifier.fillMaxWidth()
                    .fontSize(22.px)
                    .fontWeight(FontWeight.Bold)
                    .color(eparishColors.text),
                horizontalArrangement = Arrangement.Start,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Text("Orodha ya Wakatoliki")

                Span(attrs = Modifier.weight(1f).toAttrs())

                Button(
                    modifier = buttonStyle.toModifier(),
                    onClick = { sajiliMkatoliki = true }
                ) {
                    Row(
                        verticalAlignment = Alignment.CenterVertically,
                        horizontalArrangement = Arrangement.spacedBy(6.px)
                    ) {
                        FaPlus(size = IconSize.XL, modifier = Modifier.color(eparishColors.text))

                        if (isLargeScreen) {
                            Text("Sajili Mkatoliki")
                        }
                    }
                }
            }

            /* ---------------
               Add your list here later
               --------------- */

        } else {

            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .fontSize(22.px)
                    .fontWeight(FontWeight.Bold)
                    .color(eparishColors.text)
            ) {
                Registration() // your form
            }
        }
    }
}

/* ------------------------------
   Button Gradient + Hover Style
------------------------------- */
val buttonStyle = CssStyle {
    base {
        Modifier
            .borderRadius(12.px)
            .color(eparishColors.text)
            .padding(topBottom = 8.px, leftRight = 12.px)
            .backgroundImage(
                linearGradient(eparishColors.primary, eparishColors.third)
            )
            .boxShadow(
                offsetX = 0.px,
                offsetY = 3.px,
                blurRadius = 10.px,
                color = eparishColors.text
            )
    }
    hover {
        Modifier.boxShadow(
            offsetX = 0.px,
            offsetY = 3.px,
            blurRadius = 10.px,
            color = Color.black
        )
    }
    active {
        Modifier.boxShadow(
            offsetX = 0.px,
            offsetY = 3.px,
            blurRadius = 10.px,
            color = eparishColors.primary
        )
    }
}
